import fetch from "isomorphic-unfetch";
import { withApollo } from "react-apollo";

const Redirect = props => {
  console.log(props);
  return <h1>hey</h1>;
};

Redirect.getInitialProps = async ({ url, client }) => {
  console.log(url);
  try {
    const { data } = await client.query({
      query: GET_ORIGINAL,
      variables: { short: url }
    });
    console.log(data);
    return { data: data.getOriginal };
  } catch (err) {
    return { error: "Not found" };
  }
};

export default withApollo(Redirect);
